<template>
  <div class="login">
    <!-- <van-form>
      <van-field label="用户名" v-model="mobile" placeholder="输入用户名"></van-field>
      <van-field label="密码" type="password" v-model="pwd" placeholder="输入密码"></van-field>
      <van-button type="danger" block @click="doLogin">立即登陆</van-button>
    </van-form>-->
    <div class="login-box">
      <p>登录账号</p>
      <p>虾米-严选商城欢迎你</p>
      <!-- 输入账号或者手机号 -->
      <p :class="{'action':sjh,'action1':sjh1 }">
        <input autofocus="autofocus" @blur="shouji" v-model="mobile" type="tel" placeholder="手机号码" />
        <span v-show="sjh">必填</span>
      </p>
      <!-- 输入密码 -->
      <p :class="{'action':flag1,'action1':pwd1 }">
        <input @blur="mima" v-show="!flag" v-model="pwd" type="password" placeholder="密码" />
        <van-icon @click="lodj" v-show="!flag" name="closed-eye" />

        <input @blur="mima" v-show="flag" v-model="pwd" type="text" placeholder="密码" style="display: none;" />
        <van-icon @click="lodj" v-show="flag" name="eye-o" />
      </p>
      <!-- 登录按钮 -->
      <div class="pay" @click="doLogin">
        <a data-animation="ripple">
          登
          <span style="font-weight: bold;">录</span>
        </a>
      </div>
      <!-- 忘记密码   修改密码 -->
      <p class>忘记密码</p>
      <!-- 还没注册？立即注册 -->
      <p @click="zhuce" class>还没有注册？ 立即注册</p>
    </div>
  </div>
</template>

<script>
import qs from "qs";
import axios from "axios";
export default {
  data() {
    return {
      //密码
      pwd: "",
      flag: false,
      flag1: false,
      pwd1:false,
      //手机号
      mobile: "",
      sjh: false,
      sjh1: false,
    };
  },
  mounted() {
    document.title = "登录";
  },
  methods: {
    doLogin() {
      var data = {
        mobile: this.mobile,
        pwd: this.pwd
      };
      axios
        .post(
          "https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",
          qs.stringify(data)
        )
        .then(res => {
          console.log(res);
          if (res.data.msg != "success") {
            this.$toast.fail("账号密码不正确");
            return false;
          } else {
            localStorage.setItem("yx-token", res.data.data.token);
            // alert("登录成功")
            this.$router.go(-1);
          }
        });
    },
    lodj() {
      this.flag = !this.flag;
    },
    // 跳转注册页面
    zhuce() {
      this.$router.push("/zhuce");
    },
    // 验证手机号
    shouji() {
      if (this.mobile == "") {
        this.sjh = true;
        this.$notify({ type: "warning", message: "请输入正确手机号" });
      } else if (this.mobile != "") {
        this.sjh1 = true;
      }
    },
    // 验证密码
    mima() {
      if (this.pwd == "") {
        this.flag1 = true;
        this.$notify({ type: "warning", message: "请输入正确密码" });
      } else if (this.pwd != "") {
        this.pwd1 = true;
      }
    },
  }
};
</script>

<style lang="scss" scoped>
.login{
  padding: 0.26rem;
    _height: 2rem;
  min-height: 2rem;
}
.login-box {
  width: 4.8rem;
  padding: 0 0.44rem;
  // min-height: 10rem;
  border: 0.02rem solid #f5f5f5;
  margin: 0.26rem auto;
  p:nth-of-type(1) {
    font-size: 0.3rem;
    color: #666;
    margin-top: 0.44rem;
  }
  p:nth-of-type(2) {
    font-size: 0.26rem;
    color: #909090;
    margin-top: 0.36rem;
  }
  p:nth-of-type(3) {
    width: 4.72rem;
    height: 0.66rem;
    border-radius: 0.08rem;
    background: #f5f5f5;
    margin-top: 0.44rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    border: 1px solid #f5f5f5;
    justify-content: space-between;
    padding: 0 0.2rem 0 0;
    input {
      margin-left: 0.2rem;
      width: 2.6rem;
      height: 0.4rem;
      border: none;
      outline: none;
      background: #f5f5f5;
      font-size: 0.2rem;
      color: #bfbfbf;
    }
  }
  p:nth-of-type(4) {
    width: 4.72rem;
    height: 0.66rem;
    border-radius: 0.08rem;
    background: #f5f5f5;
    margin-top: 0.44rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: space-between;
    padding: 0 0.2rem 0 0;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    border: 1px solid #f5f5f5;
    input {
      margin-left: 0.2rem;
      width: 2.6rem;
      height: 0.4rem;
      border: none;
      outline: none;
      background: #f5f5f5;
      font-size: 0.2rem;
      color: #bfbfbf;
    }
  }
  .pay {
    height: 50px;
    width: 200px;
    margin-left: 0.66rem;
    font-size: 0.24rem;
    margin-top: 0.5rem;
    a {
      height: 100%;
      width: 2.9rem;
      display: block;
      outline: none;
      padding: 20px;
      color: #fff;
      text-transform: uppercase;
      background: linear-gradient(135deg, #e570e7 0%, #79f1fc 100%);
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      text-align: center;
      line-height: 14px;
      font-family: roboto, helvetica;
      font-weight: 200;
      letter-spacing: 1px;
      text-decoration: none;
      -webkit-box-shadow: 0 5px 3px rgba(0, 0, 0, 0.3);
      box-shadow: 0 5px 3px rgba(0, 0, 0, 0.3);
      cursor: pointer;
      -webkit-tap-highlight-color: transparent;
      border-radius: 5px;
    }
  }
  p:nth-of-type(5) {
    font-size: 0.16rem;
    color: #989898;
    width: 100%;
    margin-left: 1.7rem;
    margin-top: 0.36rem;
  }
  p:nth-of-type(6) {
    color: #549ff9;
    font-size: 0.26rem;
    width: 100%;
    margin-left: 0.66rem;
    margin-top: 0.38rem;
  }
}
.action {
  border: 0.01rem solid rgb(225, 44, 30) !important;

}
.action1 {
      border: 0.01rem solid rgb(56, 219, 36) !important;
      background: rgb(225, 255, 223) !important;
      input {
        background: rgb(225, 255, 223) !important;
      }
      span {
        display: none;
      }
    }
</style>


