<template>
  <div class="sku">
    <div class="sku-top">
      <img width="30" :src="basicInfo.pic" alt />
      <div class="sku-top-rigth">
        <p v-html="basicInfo.name"></p>
        <p>￥{{basicInfo.minPrice}}</p>
        {{this.title1}}
      </div>
    </div>
    <div class="sku-gg">
      <div class="sku-box" v-for="(itme,key) in skugg">
        <p class="sku-p">{{itme.name}}</p>
        <div class="sku-list">
          <div :class="{'action':k==num}" v-for="(v,k) in itme.childsCurGoods">
            <div @click="djcolor(v.name,k)" class="sku-itme">{{v.name}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="goos">
      <p>购买数量</p>
      <div>
        <van-stepper v-model="value" @plus="aa" />
      </div>
    </div>
    <van-button @click="add(basicInfo)" type="danger" size="large">加入购物车</van-button>
  </div>
</template>

<script>
export default {
  name: "Skupoup",
  props: ["skushow", "basicInfo", "skugg"],
  data() {
    return {
      num: 0,
      title1: "",
      value: 1,
      check:false
    };
  },
  methods: {
    djcolor(v, k) {
      this.num = k;
      this.title1 = v;
      //   console.log(this.title1);
    },
    aa() {
      // console.log(this.value)
    },
    add(itme) {
      this.$toast.success("加入购物成功");
      this.$nextTick(() => {
        setTimeout(() => {
          let token = localStorage.getItem("yx-token");
          if (!token) {
            this.$toast.fail("请先登陆账号");
            this.$router.push("/login");
            return false;
          } else {
            console.log("已登录账号");
            if (this.title1 == "") {
              this.$toast("请选择规格");
              return false;
            }
            let obj = new Object();
            obj.id = itme.id;
            obj.img = itme.pic;
            obj.name = itme.name;
            obj.price = itme.minPrice;
            obj.tit = this.skugg[0].name;
            obj.tit1 = this.title1;
            obj.num = this.value;
            obj.check=this.check
            this.$store.commit("add", obj);
            this.$router.push("/home/goods");
          }
        }, 1000);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.sku {
  .sku-top {
    width: 100%;
    border-bottom: 1px solid gainsboro;
    padding: 0.2rem;
    display: flex;
    img {
      width: 1.4rem;
      height: 1.4rem;
    }
    .sku-top-rigth {
      width: 4rem;
      margin-left: 0.3rem;
      p:nth-of-type(1) {
        width: 100%;
        margin-top: 0.3rem;
      }
      p:nth-of-type(2) {
        width: 100%;
        margin-top: 0.3rem;
        color: red;
      }
    }
  }
  .sku-gg {
    width: 100%;
    padding: 0.4rem 0;
    border-bottom: 1px solid gainsboro;
    .sku-box {
      width: 100%;
      height: 100%;

      .sku-p {
        font-size: 0.2rem;
        margin-left: 0.24rem;
        // margin-top: .2rem;
      }
      .sku-list {
        width: 100%;
        display: flex !important;
        // justify-content: space-between !important;
        // align-items: center !important;
        .sku-itme {
          //   width: 30%;
          padding: 0.1rem 0.1rem;
          border-radius: 0.06rem;
          margin-top: 0.1rem;
          margin-left: 0.1rem;
          border: 0.01rem solid gainsboro;
        }
        .action {
          .sku-itme {
            border: 0.01rem solid #b6272d;
          }
        }
      }
    }
  }
  .goos {
    width: 100%;
    display: flex;
    padding: 0.3rem;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
  }
}
</style>


