<template>
  <!-- 分类页面 -->
  <div class="fenye">
    <!-- 顶部搜索 -->
    <p>
      <input v-model="title" @input="seek" data-v-305c369e placeholder="回车搜索商品" type="text" />
    </p>
    <!-- 内容区域 -->
    <div class="fenye-box">
      <!-- 左侧 -->
      <div class="left">
        <!-- 左侧导航 -->
        <ul>
          <li @click="all" :class="{active:num==0}">全部分类</li>
          <li
            :class="{active:(key+1==num)}"
            @click="getData(itme.id,key+1)"
            v-for="(itme,key) in list"
            v-if="itme.pid==0"
          >{{itme.name}}</li>
        </ul>
      </div>
      <!-- 右侧 -->
      <div class="rigth">
        <!-- swiper轮播 -->
        <van-swipe class="my-swipe" :autoplay="3000">
          <van-swipe-item v-for="(itme,key) in len" :key="itme.id">
            <img :src="itme.img" alt />
          </van-swipe-item>
        </van-swipe>
        <!-- 筛选数据 -->
        <ul v-show="!show">
          <li @click="add(itme.id)" v-for="(itme,key) in newlist" :key="itme.id">
            <img :src="itme.icon" alt />
            <p>{{itme.name}}</p>
          </li>
        </ul>
        <!-- 搜索数据 -->
        <ul v-show="show">
          <li v-for="(itme,key) in lent" :key="itme.id">
            <img :src="itme.icon" alt />
            <p>{{itme.name}}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      num: 0,
      newlist: [],
      flag: false,
      len: [
        { id: 1, img: "/static/1.jpg" },
        { id: 2, img: "/static/2.jpg" },
        { id: 3, img: "/static/3.jpg" },
        { id: 4, img: "/static/4.jpg" }
      ],
      lent: [],
      title: "",
      show: false
    };
  },
  mounted() {
    // this.getlist();
    // this.all();
    // this.getnewlist();
  },
  created(){
    this.getlist();
    this.all();
  },
  methods: {
    // 获取列表
    getlist() {
      this.$axios
        .post("https://api.it120.cc/small4/shop/goods/category/all")
        .then(res => {
          // console.log(res);
          this.list = res;
        });
    },
    // 左侧导航筛选出右侧的数据
    getData(id, k) {
      this.show = false;
      console.log(id);
      this.num = k;
      var arr = this.list.filter(itme => {
        return itme.pid == id;
      });
      this.newlist = arr;
    },
    // 全部列表数据
    all() {
      this.$axios
        .post("https://api.it120.cc/small4/shop/goods/category/all")
        .then(res => {
          this.newlist = res;
        });
        this.num=0
    },
    // 搜索
    seek() {
      this.lent = [];
      if (this.title == "") {
        return false;
      }
      for (var i in this.list) {
        if (this.list[i].name.indexOf(this.title) >= 0) {
          this.lent.push(this.list[i]);
        }
      }
      this.show = true;
      this.title = "";
    },
    add(id){
      this.$toast.setDefaultOptions(this.$toast("加载中"),{ duration: 100 ,});
      this.$nextTick(() => {
        setTimeout(() => {
            this.$router.push("/classifyxq?id="+id)
        }, 1000);
      });
      
      // this.$router.push({path:"/classifyxq",query:{id:id}})
    }
  }
};
</script>

<style lang="scss" scoped>
.fenye {
  width: 100%;
  height: 100%;
  p:nth-of-type(1) {
    width: 100%;
    height: 0.6rem;
    border-bottom: 0.01rem solid #f3f3f3;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    input {
      font-size: 0.2rem;
      width: 3.47rem;
      height: 0.35rem;
      background: #f5f5f5;
      border: none;
      border-radius: 0.2rem;
      margin-left: 0.2rem;
      padding-left: 0.2rem;
    }
  }
}
.fenye-box {
  width: 100%;
  display: flex;
  position: relative;
  .left {
    width: 25%;
    border-right: 1px solid #E9E9E9;
    position: fixed;
    top: .64rem;
    left: 0;
    height: 100%;
    ul {
      width: 100%;
      li {
        margin-top: 0.3rem;
        text-indent: .3rem
      }
    }
  }
  .rigth {
    width: 75%;
    position: fixed;
    top: .64rem;
    left: 26%;
    overflow:auto;
    height: 100%;
    ul {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      margin-top: 0.2rem;
      // height: 100%;
      li {
        width: 33%;
        img {
          width: 1.1rem;
          height: 1.1rem;
          display: block;
          margin: 0 auto;
        }
        p {
          font-size: 0.16rem;
          color: #3e4561;
          margin-top: 0.05rem;
          text-align: center;
          display: block;
        }
      }
    }
  }
}
.active {
  border-left: 1px solid #d41515;
  color: #d41515;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  height: 2rem;
  text-align: center;
  // background-color: #39a9ed;
  img {
    width: 100%;
  }
}
</style>


