<template>
  <div class="details">
    <!-- 详情页轮播 -->
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(item,index) in Sbanner" :key="index">
        <img :src="item.pic" />
      </van-swipe-item>
    </van-swipe>
    <!-- 详情页name属性 -->
    <div class="navlist">
      <p v-html="basicInfo.name"></p>
      <p>{{basicInfo.characteristic}}</p>
      <div>
        <p>
          底价
          <span>￥{{basicInfo.minPrice}}</span>
        </p>
        <p>原价：{{basicInfo.originalPrice}}</p>
        <p>库存：{{basicInfo.stores}}</p>
      </div>
    </div>
    <!-- 详情页商品详情 -->
    <van-tabs class="goods-van">
      <van-tab title="商品详情">
        <div class="detail">
          <div id="hhh" v-html="goodstitle"></div>
        </div>
      </van-tab>
      <van-tab title="商品评价">
        <ul class="ul">
          <li v-for="(itme,key) in goodspl" :key="key">
            <van-icon name="https://b.yzcdn.cn/vant/icon-demo-1126.png" />
            <div class="div1">
              <div>
                <p>匿名用户</p>
                <p>{{itme.goods.goodReputation?"好评":"差评"}}</p>
              </div>
              <p>{{itme.goods.goodReputationRemark}}</p>
              <div>
                <p>{{itme.goods.dateReputation}}</p>
                <p>
                  选择规格:
                  <span>{{itme.goods.property}}</span>
                </p>
              </div>
            </div>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
    <div @click="cykj" class="kj">立即发起砍价，最低可砍到1元</div>
  </div>
</template>

<script>
export default {
  name: "Details",
  data() {
    return {
      Sbanner: [],
      basicInfo: [],
      goodstitle: "",
      goodspl: []
    };
  },
  mounted() {
    var id = this.$route.params.id;
    this.$axios
      .get("https://api.it120.cc/small4/shop/goods/detail?id=" + id)
      .then(res => {
        // console.log(res);
        this.Sbanner = res.pics;
        this.basicInfo = res.basicInfo;
        this.goodstitle = res.content;
      });
    this.$axios
      .post("https://api.it120.cc/small4/shop/goods/reputation?goodsId=" + id)
      .then(res => {
        this.goodspl = res;
        console.log(res);
        //   res.forEach((itme)=>{
        //       console.log(itme)
        //   })
      });
  },
  updated() {
    let obj = document.getElementById("hhh");
    let imgs = obj.getElementsByTagName("img");
    for (let i = 0; i < imgs.length; i++) {
      imgs[i].style.width = "100%";
      imgs[i].style.height = "100%";
    }
    let li = obj.getElementsByTagName("li");
    for (let i = 0; i < li.length; i++) {
      li[i].style.display = "flex";
      li[i].style.padding = ".2rem";
    }
  },
  methods:{
    cykj(){
      
      let token = localStorage.getItem("yx-token");
      if(!token){
        this.$toast.fail("请先登陆账号");
        this.$router.push("/login");
        return false;
      }else{
        console.log("已登录账号")
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.van-swipe {
  width: 100%;
  height: 6rem;
  .van-swipe-item {
    width: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.navlist {
  width: 6rem;
  height: 1.54rem;
  background: #ffffff;
  padding-left: 0.2rem;
  border-bottom: 0.14rem solid #f5f5f5;
  p:nth-of-type(1) {
    font-size: 0.26rem;
    color: #464646;
    margin-top: 0.2rem;
  }
  p:nth-of-type(2) {
    font-size: 0.18rem;
    color: #b8b8b8;
    margin-top: 0.12rem;
  }
  div:nth-of-type(1) {
    display: flex;
    margin-top: 0.2rem;
    align-items: center;
    box-sizing: border-box;
    p:nth-of-type(1) {
      font-size: 0.16rem;
      color: red;
      width: 1rem;
      margin-top: 0.06rem;
      span {
        font-size: 0.22rem;
      }
    }
    p:nth-of-type(2) {
      font-size: 0.22rem;
      color: #b8b8b8;
      width: 3.4rem;
      margin-top: 0.06rem;
    }
    p:nth-of-type(3) {
      font-size: 0.22rem;
      color: #b8b8b8;
      margin-top: 0.08rem;
    }
  }
}
.ul {
  li {
    width: 100%;
    display: flex;
    padding: 0 0.3rem;
    border-bottom: 0.01rem solid #e9e9e9;
    height: 1.8rem;
    i {
      margin-top: 0.28rem;
      font-size: 0.9rem;
    }
    .div1 {
      margin-left: 0.14rem;
      div:nth-of-type(1) {
        display: flex;
        align-items: center;
        margin-top: 0.18rem;
        p:nth-of-type(2) {
          width: 0.48rem;
          height: 0.4rem;
          border-radius: 0.06rem;
          color: white;
          text-align: center;
          line-height: 0.4rem;
          background: #b7282e;
          font-size: 0.16rem;
          margin-left: 0.2rem;
        }
      }
    }
    p:nth-of-type(1) {
      margin-top: 0.1rem;
      font-size: 0.16rem;
    }
    div:nth-of-type(2) {
      display: flex;
      margin-top: 0.2rem;
      p:nth-of-type(1) {
        font-size: 0.12rem;
        color: #b2b2b2;
      }
      p:nth-of-type(2) {
        margin-left: 0.25rem;
        font-size: 0.12rem;
        color: #b2b2b2;
        width: 3rem;
      }
    }
  }
}
.details {
  width: 100%;
  height: 100%;
}
.kj {
  width: 100%;
  height: 0.6rem;
  background: #b7282e;
  font-size: 0.16rem;
  color: white;
  text-align: center;
  line-height: 0.6rem;
  position: fixed;
  bottom: 0;
  left: 0;
}
</style>


