<template>
  <!--专栏详情页  -->
  <div>
    <div class="top">
      <van-icon @click="fh" name="arrow-left" />
      <p>专栏详情</p>
      <p></p>
    </div>
    <ul class="ul">
      <li>
        <p>{{list.title}}</p>
        <img :src="list.pic" alt />
        <div v-html="list.content"></div>
      </li>
    </ul>
    <div class="nav">
      <div class="zt-top">
        <p>猜你喜欢</p>
      </div>
      <ul class="ul1">
        <li @click="add(itme.id)" v-for="(itme,key) in list1" :key="itme.id">
          <img :src="itme.pic" alt />
          <p v-html="itme.name"></p>
          <p v-html="itme.characteristic"></p>
          <div>
            <span>￥{{itme.minPrice}}</span>
            <span>已售{{itme.numberSells}}</span>
          </div>
          <div v-html="itme.name"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      list: [],
      list1: []
    };
  },
  mounted() {
    this.getdeta();
    this.getxh();
    document.title = "专栏详情";
  },
  methods: {
    getdeta() {
      var id = this.$route.params.id;
      this.$axios
        .get("https://api.it120.cc/small4/cms/news/detail?id=" + id)
        .then(res => {
          // console.log(res);
          this.list = res;
        });
    },
    fh() {
      this.$router.push("/home/Index");
    },
    // 猜你喜欢 列表
    getxh() {
      this.$axios("https://api.it120.cc/small4/shop/goods/list").then(res => {
        var arr = res;
        function getRandom(n) {
          var newarr = [],
          leng = arr.length;
          for (var i = 0; i < n; i++) {
            var str = Math.floor(Math.random() * leng);
            newarr.push(arr[str]);
          }
          console.log(leng);
          return newarr;
        }
        var aa = getRandom(4);
        this.list1 = aa;

        
      });
    },
    add(id){
      this.$router.push({name: "Details",params: {id: id }});
    }
  }
};
</script>

<style lang="scss" scoped>
.zt-top {
  width: 100%;
  height: 0.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.top {
  width: 100%;
  height: 0.7rem;
  background: #ffffff;
  display: flex;
  justify-content: space-between;
  -webkit-box-align: center;
  align-items: center;
  padding: 0 0.3rem;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
}
.ul {
  padding-top: 0.7rem;
  li {
    p:nth-of-type(1) {
      font-size: 0.3rem;
      color: #333333;
      width: 100%;
      text-align: center;
      margin-top: 0.45rem;
      margin-bottom: 0.2rem;
    }
    img {
      width: 96%;
      height: 3.13rem;
      display: block;
      margin: 0 auto;
    }
  }
}
.ul1 {
  width: 100%;
  padding: 1%;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 0.2rem;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-top: 0.2rem;
  border-bottom: 0.14rem solid #f5f5f5;
  li {
    width: 48%;
    position: relative;
    img {
      width: 100%;
      height: 3.5rem;
      display: block;
    }
    p:nth-of-type(1) {
      font-size: 0.2rem;
      color: #000000;
      width: 2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    p:nth-of-type(2) {
      font-size: 0.14rem;
      color: #b2b2b2;
      width: 2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin-top: 0.1rem;
    }
    div:nth-of-type(1) {
      display: flex;
      // width: 98%;
      justify-content: space-between;
      align-items: center;
      padding: 1%;
      font-size: .26rem;
      span:nth-of-type(1) {
        font-size: 0.26rem;
        color: #d00000;
        // margin-top: 0.1rem;
      }
      span:nth-of-type(2) {
        font-size: 0.2rem;
        color: gainsboro;
        // margin-top: 0.1rem;
      }
    }
    div:nth-of-type(2){
      width: 98%;
      position: absolute;
      top: 3.1rem;
      background: #f0edd4;
      color: #b69b72;
      padding: 1%;
      text-indent: .2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>


