<template>
  <div class="classifyxq">
    <div class="Classifyxq-top">
      <van-icon @click="fh" size="20" name="arrow-left" />
      <p>分类商品</p>
      <p></p>
    </div>
    <ul v-if="list.length!=0" class="ul1">
      <li @click="add(itme.id)" v-for="(itme,key) in list" :key="itme.id">
        <img :src="itme.pic" alt />
        <div>
          <span>￥{{itme.minPrice}}</span>
        </div>
        <div v-html="itme.name"></div>
      </li>
    </ul>
    <img v-else class="goodsimg" src="/static/goos1.jpg" alt="">
  </div>
</template>

<script>
export default {
  name: "Classifyxq",
  data() {
    return {
      list: []
    };
  },
  methods: {
    fh() {
      this.$router.go(-1);
    },
    getdata() {
      var id = this.$route.query.id;
      this.$axios
        .get("https://api.it120.cc/small4/shop/goods/list")
        .then(res => {
          console.log(res);
          var a = res.filter(itme => {
            return itme.categoryId == id;
          });
          this.list = a;
        });
    },
    add(id) {
      this.$router.push({ name: "Details", params: { id: id } });
    }
  },
  mounted() {
    this.getdata();
  }
};
</script>

<style lang="scss" scoped>
.classifyxq {
  width: 100%;
  height: 100%;
  .Classifyxq-top {
    margin: 0 auto;
    width: 90%;
    height: 0.8rem;
    // border: 1px solid ;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.ul1 {
  width: 100%;
  padding: 1%;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 0.2rem;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-top: 0.2rem;
  border-bottom: 0.14rem solid #f5f5f5;
  li {
    width: 48%;
    position: relative;
    img {
      width: 100%;
      height: 3.5rem;
      display: block;
    }
    div:nth-of-type(1) {
      display: flex;
      // width: 98%;
      justify-content: space-between;
      align-items: center;
      padding: 1%;
      font-size: 0.26rem;
      span:nth-of-type(1) {
        font-size: 0.26rem;
        color: #d00000;
      }
    }
    div:nth-of-type(2) {
      width: 98%;
      position: absolute;
      top: 3.1rem;
      background: #f0edd4;
      color: #b69b72;
      padding: 1%;
      text-indent: 0.2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
.goodsimg{
    width: 100%;
    margin-top: 2rem;
    border: none;
    background: white;
}
</style>


