<template>
  <!-- 购物车页面 -->
  <div class="goods">
    <!-- 顶部位置 -->
    <div class="goods-top">
      <p @click="bj">
        <span v-show="show">编辑</span>
        <span v-show="!show">完成</span>
      </p>
      <p>购物车</p>
      <p></p>
    </div>
    <!-- 购物车内容 -->
    <ul>
      <li v-for="(itme,key) of list">
        <div v-show="!show" @click="dianji(key)" class="left">
          <input v-model="itme.check" type="checkbox" />
        </div>
        <img :src="itme.img" alt />
        <div class="goods-left">
          <p v-html="itme.name"></p>
          <p>{{itme.tit}}</p>
          <p>规格:{{itme.tit1}}</p>
        </div>
        <p>
          <van-stepper v-model="itme.num" />
        </p>
      </li>
    </ul>
    <!-- 底部删除和下单 -->
    <div class="goods-bottom">
      <div class="goods-div" v-show="!show">
        <div @click="qx" class="checkbox">
          <input v-model="flag" type="checkbox" />
          <label>全选</label>
        </div>
        <van-button @click="del" type="danger">删除</van-button>
      </div>
      <div class="goods-div" v-show="show">
        <div @click="qx" class="checkbox">
          <input v-model="flag" type="checkbox" />
          <label>全选</label>
        </div>
        <van-button type="danger">下单</van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: this.$store.state.list,
      show: true,
      flag: false
    };
  },
  methods: {
    bj() {
      this.show = !this.show;
    },
    dianji(k) {
      console.log(this.list[k].check);
    //   this.list[k].check=!this.list[k].check
      this.$store.commit("dianji", k);
      this.flag=this.list.every((itme)=>{
          return itme.check==true
      })
    },
    qx(){
        this.flag=!this.flag
        this.list.forEach((itme)=>{
            itme.check=this.flag
        })
    },
    del(){
        this.$store.commit("del");
    }
  }
};
</script>

<style lang="scss" scoped>
.goods {
  position: relative;
  width: 100%;
  ul {
    margin-top: 0.6rem;
    width: 100%;
    li {
      border-bottom: 1px solid gainsboro;
      padding: 0.3rem;
      display: flex;
      img {
        width: 1.5rem;
        margin-left: 0.3rem;
      }
      .goods-left {
        width: 40%;
        box-sizing: border-box;
        // border: 1px solid red;
        // margin-left: 0.2rem;
        p:nth-of-type(1) {
          margin-top: 0.2rem;
          text-indent: 0.3rem;
          font-size: 0.2rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        p:nth-of-type(2) {
          margin-top: 0.2rem;
          text-indent: 0.3rem;
        }
        p:nth-of-type(3) {
          margin-top: 0.1rem;
          color: gainsboro;
          font-size: 0.18rem;
          text-indent: 0.3rem;
        }
      }
      p:nth-of-type(1) {
        margin-top: 1rem;
        // margin-right: ;
        width: 2rem;
      }
    }
  }
}
.goods-top {
  width: 100%;
  height: 0.6rem;
  border-bottom: 1px solid gainsboro;
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  p:nth-of-type(1) {
    color: red;
  }
}
.goods-bottom {
  width: 100%;
  height: 0.7rem;
  border-bottom: 1px solid gainsboro;
  border-top: 1px solid gainsboro;
  position: fixed;
  bottom: 1.2rem;
  .goods-div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.left {
  width: 0.5rem;
  margin-top: 0.8rem;
}
</style>


